@import './../../variables';

.Card { 
    display: block;
    height: 100%;
    text-align: left;
    color: inherit;
    text-decoration: none;
    background-color: var(--color-layout);
    border-radius: 6px;
    cursor: pointer;

    &_bgColor {

        &_none {
            background: transparent;
        }
    }

    &_size {

        &_small {
            width: calc((100vw - 386px)/7.5);
            min-width: 117px;
            max-width: 162px;
            background-color: transparent;

            .Card-Thumb {
                border-radius: 4px;
                transition: transform 150ms;

                &:after {
                    padding: 150% 0 0;
                }
            }
        
            .Card-Thumb:hover {
                transform: translateY(-6px);
            }

            .Card-Content {
                padding-right: 0;
                padding-left: 0;
            }
        }

        &_medium {
            width: calc(((100vw - 386px)/7.5)*1.5 + 5px);
            min-width: 180.5px;
            max-width: 248px;
        }

        &_big {
            width: 276px;
        }

        &_full {
            width: 100%;
        }
    }

    &-Thumb {
        position: relative;
        display: flex;
        align-items: flex-end;
        background: center no-repeat var(--color-placeholder);
        background-size: cover;
        border-radius: 6px 6px 0 0;
        will-change: transform;

        &:after {
            content: '';
            display: block;
            padding: 56% 0 0;
        }

        &_view {

            &_noClipped {
                border-radius: 6px;
            }

            &_full {
                height: 100%;
                border-radius: 6px;
            }
        }

        &_shadowed:before {
            content: '';
            position: absolute;
            width: 100%;
            height: 100%;
            background: linear-gradient(180deg,transparent 0,rgba(28,29,38,.5) 100%);
        }
    }  
    
    &-Poster {
        position: absolute;
        right: 10px;
        bottom: -10px;
        width: 100px;
        border-radius: 4px;
    }

    &-Details {
        position: absolute;
        bottom: 8px;
        left: 8px;
        z-index: 1;
        display: flex;
    }

    &-Content {
        z-index: 1;
        padding: 8px 10px 12px;
        overflow: hidden;
        text-align: left;

        &_view_grid{
            display: flex;
            justify-content: space-between;
        }
    }

    &-Title {
        display: -webkit-box;
        max-height: 35px;
        margin-bottom: 3px;
        overflow: hidden;
        font-size: 13px;
        color: var(--text-primary);
        white-space: normal;
        text-overflow: ellipsis;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
        word-wrap: break-word;
        word-break: break-word;
        hyphens: auto;

        &_lines_one {
            -webkit-line-clamp: 1;
        }

        &_bold {
            font-weight: 700;
            font-size: 14px;
        }
    }

    &-Subtitle {
        display: -webkit-box;
        overflow: hidden;
        font-size: 12px;
        line-height: 16px;
        color: var(--text-subtitle);
        white-space: normal;
        text-overflow: ellipsis;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 1;

        &_bold {
            font-weight: 700;
        }
    }

    &-Left {
        max-width: calc(100% - 76px);
    }

    &-Right {
        display: flex;
        align-items: flex-end;
        padding-left: 10px;
    }
}
